<template>
  <!-- 背景音乐 -->
  <audio id="GameBGAudio" loop preload="auto" ref="audioRef">
    <source src="../assets/audio/game_music.ogg" type="audio/ogg" />
    <source src="../assets/audio/game_music.mp3" type="audio/mpeg" />
  </audio>
  <!-- 绘制场景盒子 -->
  <div class="GameBoxBg">
    <!-- <img class="d" id="newImg" src="" /> -->
    <!-- 绘制头部 -->
    <div class="GameHeader">
      <div>
        <!-- 分数盒子 -->
        <div class="scoreBox">
          <span class="scoreTitle"><strong>得分: </strong></span>
          <strong
            ><span class="scoreNum"> {{ scoreValue }}</span></strong
          >
        </div>
        <div class="ctBox">
          <p>余额：<span id="ct"></span> CT</p>
        </div>
        <div class="lolgBox">
          <img src="../assets/image/打灰太狼.png" />
        </div>
      </div>
      <div class="listBOx">
        <!-- 分数盒子 -->
        <div class="timeBox">
          <strong
            ><span class="timeNum">{{ gemeTime }}</span> 秒</strong
          >
        </div>
      </div>
    </div>
    <!-- 右侧功能插件控件 -->
    <div class="controlsBox">
      <ul>
        <!-- 背景音乐按钮 -->
        <li class="musicBox">
          <button
            type="button"
            id="musicBtn"
            :class="{ musicS: isPlay }"
            @click="audioPlay"
          ></button>
        </li>
        <!-- <li class="rankingBox">
          <button
            type="button"
            class="btn_ranking"
            data-toggle="modal"
            data-target="#Gamelist"
          ></button>
          <p>榜单</p>
        </li> -->
      </ul>
    </div>
    <div class="startBox" v-if="initShow">
      <button class="start" @click="startGame">开始游戏</button>
      <!-- <button
        type="button"
        class="btn_rules"
        data-toggle="modal"
        data-target="#exampleModalScrollable"
      >
        游戏规则
      </button> -->
    </div>

    <div class="gameOverBox" v-if="gameOver">
      <h1 class="over">GAME OVER</h1>
      <div class="settlementBox">
        <p class="settlementTitle">
          您的得分：<span id="settlementNum"> {{ scoreValue }}</span>
        </p>
      </div>
      <button class="restart" @click="reset">重新开始</button>
      <!-- <button
        type="button"
        class="btn_rules"
        data-toggle="modal"
        data-target="#exampleModalScrollable"
      >
        游戏规则
      </button> -->
    </div>
  </div>
</template>

<script setup>
import { star, scoreValue, circle } from "@/assets/js/init";
import { onMounted, ref } from "vue";
let gemeTime = ref(30);
let initShow = ref(true);
let gameOver = ref(false);
const isPlay = ref(null);
const audioRef = ref();
let timing = ref(null);
const startGame = () => {
  let audioPlay = audioRef.value;
  audioPlay.play();
  isPlay.value = true;
  initShow.value = false;
  timer(gemeTime.value);
  star();
};
const audioPlay = () => {
  let audioPlay = audioRef.value;
  console.log(audioPlay.paused);
  audioPlay.paused ? audioPlay.play() : audioPlay.pause();
  isPlay.value = !audioPlay.paused;
};

const reset = () => {
  gemeTime.value = 30;
  gameOver.value = false;
  scoreValue.value = 0;
  timer(gemeTime.value);
  star();
};
const timer = (intDiff) => {
  timing = window.setInterval(function () {
    let day = 0,
      hour = 0,
      minute = 0,
      second = 0; //秒时间默认值
    if (intDiff > 0) {
      day = Math.floor(intDiff / (60 * 60 * 24));
      hour = Math.floor(intDiff / (60 * 60)) - day * 24;
      minute = Math.floor(intDiff / 60) - day * 24 * 60 - hour * 60;
      second =
        Math.floor(intDiff) - day * 24 * 60 * 60 - hour * 60 * 60 - minute * 60;
    }
    if (minute <= 9) minute = "0" + minute;
    if (second <= 9) second = "0" + second;
    // console.log(day + "天"+hour + '时'+minute + '分'+second + '秒')
    // 赋值给页面，倒计时时间。
    gemeTime.value = second;
    intDiff--;
    // 判断当秒数为0的时候停止运行
    if (second == "00") {
      clearInterval(timing);
      clearInterval(circle);
      console.log(second + "秒，被停止");
      // 设置延时500毫秒，防止错误视觉交互，
      setTimeout(function () {
        // 展示结束UI
        gameOver.value = true;
        // $(".scoreBox").toggle();
        // $(".listBOx").toggle();
        // $(".lolgBox").toggle();
      }, 500);
    }
  }, 1000);
};
onMounted(() => {});
</script>

