/* eslint-disable*/
import { ref } from 'vue'
let s = 0;
let scoreValue = ref(0)
let circle = ref(null)

// 随机函数

function rand(min, max) {
    return Math.round(Math.random() * (max - min) + min);
}
// var secs = rand(1000, 2000); //多久出现下一只
const secs = rand(500, 1500);
const stay = rand(150, 250);
// 游戏开始函数
const star = () => {
    if (circle) clearInterval(circle)
    var Htc = 0;
    var Xtc = 0;
    var audioNum = 0
    //=============================================================================游戏进行时
    circle = setInterval(function () {
        //灰太狼随机出现的位置
        var arrPos = [{
            left: "2.25rem",
            top: "10.9375rem"
        },
        {
            left: "9.0625rem",
            top: "9.4375rem"
        },
        {
            left: "15.125rem",
            top: "11.625rem"
        },
        {
            left: "2.25rem",
            top: "17.75rem"
        },
        {
            left: "9.1875rem",
            top: "16.3125rem"
        },
        {
            left: "15.125rem",
            top: "18.5rem"
        },
        {
            left: "2.125rem",
            top: "27rem"
        },
        {
            left: "8.875rem",
            top: "25.4375rem"
        },
        {
            left: "15.125rem",
            top: "27.625rem"
        }
        ];
        // 将图片存进数组
        // 灰太狼，
        var wolf_1 = [require('../image/h0.png'), require('../image/h1.png'), require('../image/h2.png'), require('../image/h3.png'), require('../image/h4.png'), require('../image/h5.png'), require('../image/h6.png'), require('../image/h7.png'), require('../image/h8.png'), require('../image/h9.png')
        ];
        // 小灰狼
        var wolf_2 = [require('../image/x0.png'), require('../image/x1.png'), require('../image/x2.png'), require('../image/x3.png'), require('../image/x4.png'), require('../image/x5.png'), require('../image/x6.png'), require('../image/x7.png'), require('../image/x8.png'), require('../image/x9.png')
        ];
        // 跳出动画
        var appearWolf = [require('../image/h0.png'), require('../image/x0.png')];
        // 创建一个img标签
        var newImg = document.createElement('img');
        // container盒子内添加img标签
        // $(".GameBoxBg").append(newImg);
        document.getElementsByClassName('GameBoxBg')[0].appendChild(newImg);
        // //  一共9个位置，狼的随机一个位置出现
        var wfLocation = rand(0, 8);
        // // 给新建的img标签添加左边距
        newImg.style.width = "6.75rem";
        // 防止js加载时，Img出现占位白边，此处属性设置Auto自适应
        newImg.style.height = "auto";
        newImg.style.left = arrPos[wfLocation].left;
        // 给新建的img标签添加顶部边距
        newImg.style.top = arrPos[wfLocation].top;

        // 给新建的img标签属性，设置为根据自己的位置定位
        newImg.style.position = 'absolute';
        // 选择灰太狼还是小灰灰
        var X = rand(0, 1);
        // console.log(X)
        // 当X<1的时候，选择正确
        if (X == 1) {

            Htc++;
            // 等于1时为正确
            X = 'h';
        } else {

            Xtc++;
            // 否则错误
            X = 'x';
        }
        var y = 0;
        // 图片设置为可见
        newImg.style.display = 'block';
        var appear0 = setInterval(function () {
            newImg.src = require('../image/' + X + y + '.png');
            // 当前程序每执行一次，加一
            y++;
            // 设置鼠标单击次数判断条件
            var indexs = 0;
            // 当img被点击执行函数
            newImg.onclick = function () {
                // 鼠标被点击条件加一
                indexs++;
                if (indexs > 1) {
                    // 鼠标只能点击1次 而不能无限点
                    return false;
                }
                // y等于第五帧动画图片
                y = 5;
                // 执行图片被点击的动画
                for (var i = 0; i < 4; i++) {
                    y++;
                    // 每执行一次赋值给当前被点击的标签做动效
                    newImg.src = wolf_1[y];

                    if (y > 9) {
                        y--;
                    }
                };
                // 当点击图片是正确
                if (X == 'h') {
                    // 添加分数
                    s += 10;
                    scoreValue.value = s
                    // 将当前的数值赋值到HTML分数中显示
                    // $(".scoreNum").text(s);
                    // 播放一次音乐
                    audioNum++;
                    let audioS = document.createElement('audio') //生成一个audio元素 
                    audioS.id='secondAudio'+audioNum
                    audioS.className='second'
                    // audio.controls = true //这样控件才能显示出来 
                    audioS.src = require('../audio/second_music.mp3')
                    document.body.appendChild(audioS) //把它添加到页面中
                    document.getElementById('secondAudio'+audioNum).play()
                    for(let i=0;i<document.getElementsByClassName('second');i++){
                        if(document.getElementsByClassName('second')[i].paused){
                            document.getElementsByClassName('second')[i].remove
                        }
                    }
                    // var audioS = '<audio class="second" id="secondAudio' + audioNum +
                    //     '" preload="auto" ><source src="audio/second_music.ogg" type="audio/ogg"><source src="audio/second_music.mp3" type="audio/mpeg"></audio>'
                    // $("body").append(audioS);
                    // $('#secondAudio' + audioNum)[0].play();
                    // $(".second").each(function () {
                    //     if (this.paused) {
                    //         this.remove();
                    //     }
                    // });
                } else if (X == 'x') {
                    // 反之单错了执行
                    s -= 10;
                    if (s <= 0) {
                        s = 0;
                    }
                    // 将当前错误的数值，赋值到HTML分数中显示
                    // $(".scoreNum").text(s);
                    // 执行一次音乐
                    scoreValue.value = s
                    audioNum++;
                    let audioS = document.createElement('audio') //生成一个audio元素 
                    audioS.id='secondAudio'+audioNum
                    audioS.className='second'
                    // audio.controls = true //这样控件才能显示出来 
                    audioS.src = require('../audio/no_hit.mp3')
                    document.body.appendChild(audioS) //把它添加到页面中
                    document.getElementById('secondAudio'+audioNum).play()
                    for(let i=0;i<document.getElementsByClassName('second');i++){
                        if(document.getElementsByClassName('second')[i].paused){
                            document.getElementsByClassName('second')[i].remove
                        }
                    }
                    // var audioS = '<audio class="second" id="secondAudio' + audioNum +
                    //     '" preload="auto" ><source src="audio/no_hit.ogg" type="audio/ogg"><source src="audio/no_hit.mp3" type="audio/mpeg"></audio>'
                    // $("body").append(audioS);
                    // $('#secondAudio' + audioNum)[0].play();
                    // $(".second").each(function () {
                    //     if (this.paused) {
                    //         this.remove();
                    //     }
                    // });
                }
            };

            if (y > 5) {
                clearInterval(appear0);
                setTimeout(function () {
                    y = 5;
                    var appear1 = setInterval(function () {
                        newImg.src = require('../image/' + X + y + '.png');
                        // console.log(y);
                        y--;
                        if (y < 0) {
                            clearInterval(appear1);
                            newImg.style.display = 'none';
                            newImg.remove();
                        }
                    }, 50)
                }, stay)
            }
        }, 50);
        // 
        // console.log("总弹出：" + (Htc + Xtc) + "次,大灰狼弹出" + Htc + "次,小灰狼弹出" + Xtc + "次")
    }, secs)
    s = 0;
    // $(".scoreNum").text(s);

    //=============================================================================游戏结束
}

export { star, scoreValue, circle }